<template>
  <main>
    <b-container class="py-3" fluid>
      <b-row>
        <b-col
          cols="12"
          :sm="showSidebar ? '8' : 12"
          :md="showSidebar ? '7' : '10'"
          offset-md="1"
          :xl="showSidebar ? '8' : 10"
        >
          <ContentRenderer v-if="!pending" :value="data">
            <ContentRendererMarkdown :value="data?.body" />
          </ContentRenderer>
        </b-col>
      </b-row>
    </b-container>
  </main>
</template>

<script setup>
import { computed } from "vue";

definePageMeta({
  layout: "lte",
});

const route = useRoute();

const { data, error, pending } = await useLazyAsyncData(
  `content-${route.path}`,
  () => queryContent().where({ _path: route.path }).findOne(),
  { server: true }
);

if (error.value) console.log("Error: %o", error.value);

useContentHead(data);

const { navigation, page } = useContent();

const showSidebar = computed(
  () => pending || data?.value?.links?.length > 0 || data?.value?.author
);
</script>
